<template>
  <transition name="move">
    <div v-show="showFlag" class="food" ref="food">
      <div class="food-content">
        <div class="image-header">
          <img :src="food.image" alt="">
          <div class="back" @click="hide">
            <i class="icon-arrow_lift"></i>
          </div>
        </div>
        <div class="content">
          <h1 class="title">{{food.name}}</h1>
          <div class="detail">
            <span class="sell-count">月售{{food.sellCount}}份</span>
            <span class="rating">好评率{{food.rating}}%</span>
          </div>
          <div class="price">
            <span class="now">￥{{food.price}}</span>
            <span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
          </div>
          <div class="cartcontrol-wrapper">
            <cartcontral :food="food"></cartcontral>
          </div> 
          <transition name="fade">
            <div @click.stop.prevent="addFood" class="buy" v-show="!food.count || food.count===0">
              加入购物车
            </div>
          </transition>
        </div>
        <div class="split" v-show="food.info"></div>
        <div class="info" v-show="food.info">
          <h1 class="title">商品介绍</h1>
          <p class="test">{{food.info}}</p>
        </div>
        <div class="split"></div>
        <div class="rating">
          <h1 class="title">商品评价</h1>
          <div class="ratingselect">
            <div class="rating-type border-1px">
              <span @click="select(2, $event)" class="block positive" :class="{'active': selectType === 2 || selectType === 'ALL'}">全部
                <!-- <span class="count">{{food.ratings.length}}</span> -->
              </span>
              <span @click="select(0, $event)" class="block positive" :class="{'active': selectType === 0}">推荐
                <!-- <span class="count">{{positives.length}}</span> -->
              </span>
              <span @click="select(1, $event)" class="block negative" :class="{'active': selectType === 1}">吐槽
                <!-- <span class="count">{{negatives.length}}</span> -->
              </span>
            </div>   
            <div @click="toggleContent" class="switch" :class="{'on': onlyContent}">
              <span class="icon-check_circle"></span>
              <span class="text" >只看有内容的评价</span>
            </div>         
          </div>
          <div class="rating-wrapper">
            <ul v-show="food.ratings && food.ratings.length">
              <li v-show="needShow(rating.rateType, rating.text)" v-for="(rating, index) in food.ratings" :key="index" class="rating-item border-1px">
                <div class="user">
                  <span>{{rating.username}}</span>
                  <img class="avatar" width="12" height="12" :src="rating.avatar">
                </div>
                <div class="time">{{rating.rateTime}}</div>
                <p class="text">
                  <span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>{{rating.text}}
                </p>
              </li>
            </ul>
            <div class="no-rating" v-show="!food.ratings || !food.ratings.length">暂无评价</div>
          </div>                  
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
  import cartcontral from './cartcontral'
  import BScroll from 'better-scroll';
  import Vue from 'vue';

export default {
  name: 'commodity',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      showFlag: false,
      selectType: 'ALL',
      onlyContent: false
    }
  },
  components:{
    cartcontral
  },
  props: {
    food: {
      type: Object,
      default: {}
    }
  },
  computed:{
    positives(){
      return this.food.ratings.filter((rating) => {
        return rating.rateType === 0
      })
    },
    negatives(){
      return this.food.ratings.filter(rating =>{
        return rating.rateType === 1
      })
    }
  },
  methods: {
    show(){
      this.selectType = 'ALL'
      this.showFlag = true;
      this.onlyContent = false;
      this.$nextTick(()=>{
        if(!this.scroll){
          this.scroll = new BScroll(this.$refs.food,{
            click: true
          })
        }
      })
    },
    hide(){
      this.showFlag = false
    },
    addFood(event){
      if(!event._constructed){
        return;
      }
      Vue.set(this.food, 'count', 1)
    },
    select(type, event){
      if(!event._constructed){
        return;
      }
      if(type === 2){
        return this.selectType = 'ALL'
      }
      this.selectType = type;
      this.scroll.refresh()
      //刷新组建用
    },
    needShow(type, text){
      if(this.onlyContent && !text){
        return false
      }
      if(this.selectType === 'ALL'){
        return true
      }else{
        return type === this.selectType
      }
    },
    toggleContent(){
      this.onlyContent = !this.onlyContent;
      this.$nextTick(() => {
        this.scroll.refresh()
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" ref="stylesheet/stylus" scoped>
@import '../assets/stylus/index.styl'
  .split
    width: 100%
    height: 16px
    border-top: 1px solid rgba(7, 17, 27, 0.1)
    border-bottom: 1px solid rgba(7, 17, 27, 0.1)
    background: #f3f5f7
  .food
    position fixed 
    left 0
    top 0
    bottom 48px
    z-index 30
    width 100%
    background-color #ffffff
    transform translate3d(0, 0, 0)
    &.move-enter-active, &.move-leave-active
      transition all 0.2s linear 
    &.move-enter, &.move-leave-active
      transform translate3d(100%, 0, 0)
    .image-header
      position relative
      width 100%
      height 0
      padding-top: 100%
      img 
        position absolute 
        top 0
        left 0
        width: 100%
        height 100%
      .back
        position absolute 
        top 10px
        left 0
        .icon-arrow_lift
          display block
          padding 10px
          font-size 20px
          color #ffffff

    .content
      position relative
      padding 18px
      .title 
        line-hieght 14px
        margin-bottom 8px
        font-size 14px
        font-weight 700
        color rgb(7, 17, 27)
      .detail
        margin-bottom 18px
        line-height 10px
        height 10px
        font-size 0
        .sell-count, .rating
          font-size 10px
          color rgb(147, 153, 159)
        .sell-count
          margin-right 12px
      .price
        font-weight 700
        line-height 24px
        .now
          margin-right 8px
          font-size 14px
          color rgb(240, 20, 20)
        .old
          text-decoration line-through
          font-size 10px
          color rgb(147, 153, 159)
      .cartcontrol-wrapper
        position absolute 
        right 12px
        bottom 12px
      .buy
        position absolute
        right 18px
        bottom 18px
        z-index 10
        line-height 24px
        padding 0 12px
        box-sizing border-box 
        border-radius 12px
        font-size 10px 
        color #fff
        background-color rgb(0, 160, 220)
        opacity 1
        &.fade-enter-active, &.fade-leave-active
          transition all 0.2s
        &.fade-enter, &.fade-leave-active
          opacity 0
          z-index -1
      
    .info
      padding 18px
      .title
        line-height 14px
        margin-bottom 6px
        font-size 14px
        color rgb(7, 17, 27)
      .text
        line-height 24px
        padding 0 8px
        font-size 12px
        color rgb(77, 85, 93)
    .rating
      font-size 10px
      color rgb(147, 153, 159)
      padding-top 18px
      .title
        line-height 14px
        margin-left 18px
        font-size 14px
        color rgb(7, 17, 27)
      .ratingselect
        .rating-type
          padding 18px 0
          margin 0 18px
          border-1px(rgba(7, 17, 27, 0.1))
          font-szie 0
          .block
            display inline-block
            padding 8px 12px
            margin-right 16px
            line-height 16px
            border-radius 1px
            font-size 12px
            color rgb(77, 85, 93)
            &.active
              color #ffffff
            .count
              margin-left 2px
              font-size 8px
            &.positive
              background: rgba(0, 160, 220, 0.2)
              &.active
                background: rgb(0, 160, 220)
            &.negative
              background: rgba(77, 85, 93, 0.2)
              &.active
                background: rgb(77, 85, 93)
        .switch
          padding 12px 18px
          line-height 24px
          border-bottom 1px solid rgba(7, 17, 27, 0.1)
          color rgb(147, 153, 159)
          font-size 0
          &.on
            .icon-check_circle
              color #00c850
          .icon-check_circle
            display inline-block
            vertical-align top
            margin-right 4px
            font-size 24px
          .text 
            dispaly inline-block
            vertical-align top
            font-size 12px
      .rating-wrapper
        padding 0 18px
        .rating-item
          position relative
          padding 16px 0
          border-1px(rgba(7, 17, 27, 0.1))
          .user
            position: absolute 
            right 0
            top 16px
            line-hieght 12px
            font-size 0
            .name
              display inline-block
              margin-right 6px
              vertical-align top
              font-size 10px
              color rgb(147, 153, 159)
            .avatar 
              border-radius 50%
          .time
            margin-bottom 6px
            line-height 12px
            font-size 10px
            color rgb(147, 153, 159)
          .text 
            line-height 16px
            font-size 12px
            color rgb(7, 17, 27)
            .icon-thumb_up, .icon-thumb_down
              margin-right 4px
              line-height 16px
              font-size 12px
            .icon-thumb_up
              color rgb(0, 160, 220)
            .icon-thumb_down
              color rgb(147, 153, 159)
        .no-rating
          padding 16px 0
          font-size 12px
          color rgb(147, 153, 159)

</style>



